@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&family=Saira+Stencil+One&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  font-weight: 400;
  overflow-x: hidden;
  overflow-y: auto;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 128rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.footer {
  margin-top: auto;
  background: #292c6d;
  padding: 1.5rem 0rem;
}

.footer p {
  font-size: 1.8rem;
  font-weight: 500;
  color: #fff;
  text-align: center;
}

.about-hero {
  background-image: -webkit-linear-gradient(
      top,
      rgba(0, 0, 0, 0.35),
      rgba(0, 0, 0, 0.35)
    ),
    url(/digital-inc/static/media/about-hero.8a047e93.jpg);
  height: 60rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.about-content-section {
  width: 110rem;
  max-width: 98%;
  margin: 0 auto;
  margin-top: -15rem;
  border: 2px solid #fafafa;
  padding: 3rem;
}

.about-story {
  background-color: #fff;
  padding: 6.5rem;
}

.about-story h2 {
  font-size: 4rem;
  font-weight: 600;
  text-transform: uppercase;
  color: #292c6d;
  text-align: center;
  padding-bottom: 2.5rem;
}

.about-story-description > p {
  font-size: 1.6rem;
  color: #292c6d;
  padding-bottom: 1.5rem;
}

@media (max-width: 768px) {
  .about-hero {
    height: 40rem;
  }

  .about-story {
    background-color: #fff;
    padding: 2.5rem;
  }

  .about-content-section {
    padding: 1.5rem;
  }
}

@media (max-width: 500px) {
  .about-content-section {
    margin-top: -8rem;
  }
}

.header {
  background: #292c6d;
  padding: 1.5rem 0rem;
}

.header h1 {
  font-family: 'Saira Stencil One', cursive;
  font-size: 4rem;
  font-weight: 400;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}

.nav-bar {
  background: #fafafa;
  padding: 1.5rem 0rem;
  text-align: center;
  border-bottom: 1.5px solid #ddd;
}

.nav-bar a {
  display: inline-block;
  font-size: 1.6rem;
  font-weight: 500;
  padding: 0 2.5rem;
  color: #292c6d;
}

.nav-bar .active {
  width: 10rem;
  text-align: center;
  padding: 0.8rem 0;
  background: #292c6d;
  color: #fff;
  border-radius: 0.5rem;
}

@media (max-width: 500px) {
  .nav-bar a {
    padding: 0 1.5rem;
  }
}

.contact {
  padding: 3.5rem 0rem;
}

.contact h2 {
  font-size: 4rem;
  text-transform: uppercase;
  font-weight: 600;
  color: #292c6d;
  text-align: center;
}

.contact p {
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  color: #292c6d;
  padding-bottom: 2.5rem;
}

.contact form {
  width: 100%;
  margin: 0 auto;
}

.contact form .form-control {
  margin: 0 auto;
  margin-bottom: 1.5rem;
  width: 65%;
}

.contact form .form-control .input-label {
  display: block;
  padding-bottom: 0.5rem;
  font-weight: 500;
}

.contact form .form-control input,
.contact form .form-control textarea {
  font-family: inherit;
  padding: 1rem;
  width: 100%;
  font-size: 1.6rem;
  color: #292c6d;
  font-weight: 400;
  border: 2px solid #eee;
  border-radius: 0.5rem;
}

.contact form .form-control textarea {
  height: 20rem;
}

.contact form .form-control input[type='submit'] {
  display: block;
  width: 25rem;
  padding: 1.2rem;
  font-size: 1.8rem;
  font-weight: 500;
  background-color: #292c6d;
  color: #fff;
  text-align: center;
  margin: 0 auto;
  border: 1.5px solid #292c6d;
  border-radius: 0.5rem;
  cursor: pointer;
}

@media (max-width: 700px) {
  .contact form .form-control {
    width: 85%;
  }
}

@media (max-width: 500px) {
  .contact form .form-control {
    width: 100%;
  }
}

.grow {
  margin: auto 0;
}

.image-gallery {
  padding: 6.5rem 0rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.image-gallery .gallery-item {
  width: 32rem;
  margin-bottom: 3.25rem;
}

.image-gallery .gallery-item img {
  width: 100%;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 300ms ease;
}

.image-gallery .gallery-item img:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

@media (max-width: 690px) {
  .image-gallery {
    flex-direction: column;
    justify-content: center;
  }

  .image-gallery .gallery-item {
    width: 85%;
    margin-bottom: 3.25rem;
  }
}

